@import url("https://fonts.googleapis.com/css2?family=Cantora+One&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Jost:wght@300&display=swap");

html,
body {
  margin: 0;
  font-family: "Jost", "Cantora One", -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: auto;
  width: auto;
  overflow-x: hidden;
  overflow-y: auto;
  background-color: whitesmoke;
  background: rgb(131,58,180);
background: linear-gradient(90deg, rgba(131,58,180,1) 0%, rgba(253,29,29,1) 50%, rgba(252,176,69,1) 100%);
}

#root {
  height: auto;
  width: auto;
  scroll-behavior: smooth;
}

.App {
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 0;
  width: 100%;
  max-height: 10000vh;
  font-size: 2vh;
  font-family: "Jost", sans-serif;
}

.Header {
  position: relative;
  width: 100%;
  max-width: fit-content;
  height: 10vh;
  display: inline-flex;
  flex-direction: row;
  justify-content: left;
  font-size: 2.5vh;
  font-family: "Cantora One", sans-serif;
}

.HeaderLogo {
  margin: 5px;
  cursor: pointer;
}

.HeaderLogo:hover {
  scale: 1.05;
  transition: 120ms ease-in-out;
}

/* .icon:hover {
  scale: 1.02;
  transform: translateY(-5);
  transition: 120ms ease-in-out;
  animation-delay: 50ms;
} */

.Navbar{
  width: 100%;
  height: 10vh;
  display: inline-flex;
  flex-direction: row;
  align-items: last baseline;
  flex-wrap: nowrap;
  justify-content: space-around;
  position: sticky;
  top: 0;
  z-index: 12;
  margin-top: 5vh;
}

Canvas {
  height: 100%;
  margin: 0;
  padding: 0;
  width: 100%;
}

.Hero {
  position: relative;
  width: 100%;
  height: 150vh;
  transition: all 2s ease-in-out;
}


.Context {
  position: relative;
  height: 100vh;
  width: 100%;
}

.Contact {
  position: relative;
  height: 70vh;
  width: 100vw;
}

.Footer {
  position: relative;
  height: 25vh;
  width: 100vw;
  bottom: 0;
  justify-items: center;
  display: flex;
  flex-direction: column;
  align-items: center;;
  align-content: space-evenly;
}

.bg-G {
  background-color: gray;
}

.icon32 {
  height: 32px; width: 32px;
}


.blankDiv {
  max-width: 100%;
  max-height: 100%;
}

.button {
  height: 20rem;
  width: 35rem;
  justify-content: center;
  align-items: center;
  color: white;
  background-color: rgb(64, 64, 64);
}


.ErrorPage {
  height: 98vh;
  width: 98vw;
  margin-inline: 1vw;
  margin-top: 1vh;
  background-color: rgb(29, 21, 49);
  color: rgb(239, 227, 207);
  font-size: larger;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 5%;
  
}

.ErrorPage h1 {
  font-size: 10vh;
}

.ErrorPage h2 {
  color: azure;
}

.Socials {
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
  gap: 5px;
  margin: 3px;
}

.Socials svg {
  padding: 4px;
}


.Socials svg:hover {
  scale: 1.2;
  background: #ececec4a;
  background-blend-mode: color-dodge;
  border-radius: 10%;
  transition: all 0.2 ease-in-out;
}

.copyright {
  background-color: black;
  color: aliceblue;
  width: 100%;
  text-align: center;
}

.Working {
  height: 300vh;
  width: 100vw;
}